
.loading_wrapper{position:fixed;top:0;width:100%;left:0;z-index:99;height:100%;background:rgba(0,0,0,0.45);text-align:center
}
.loading_wrapper img{position:absolute;top:5rem;width:1rem;left:4.5rem
}
.loading-enter,.loading-leave{opacity:0
}
.loading-enter .loading-container,.loading-leave .loading-container{transform:scale(1.1)
}


