
.modal-mask {
  position:absolute;
  display:table;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background: rgba(0,0,0,0.6);
  z-index: 9;
  text-align: center;
  transition: opacity .3s ease;
}
.modal-wrapper {
  display:table-cell;
  vertical-align:middle;
  text-align:center;
  color:#565656;
}
.modal-container {
  display: inline-block;
  width:8rem;
  padding-top: .64rem/* 24px */;
  border-radius: .06666667rem/* 2.5px */;
  background:#FFF;
}
.modal-title{
  font-size:.42666667rem/* 16px */;
  margin-bottom: .32rem/* 12px */;
}
.modal-txt {
  margin-bottom:.58666667rem/* 22px */;
  font-size:.37333333rem/* 14px */;
}
.modal-footer {
  height:1.17333333rem/* 44px */;
  line-height: 1.17333333rem/* 44px */;
  border-top:1px solid #ccc;
  color:#0074E1;
  font-size: .42666667rem/* 16px */;
}
.modal-default-close {
  display: block;
}



